.card-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  text-align: center;
}

.card-container-outer {
  display: flex;
  padding: 25px;
}

.cardbox {
  margin: 20px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  border: 1px solid lightgrey;
  width: 350px;
  height: 450px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  transition: all 0.4s ease-in-out;
}
.cardbox:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transition: all 0.4s ease-in-out;
}
.cardbox:hover .button-list-container {
  opacity: 1;
  transition: all 0.4s ease-in-out;
}
.cardbox img {
  min-width: 100%;
  height: 250px;
  height: auto;
  max-height: 250px;
  border-radius: 5px 5px 0px 0px;
  object-fit: cover;
}
.cardbox ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

.card:hover {
  text-decoration: none !important;
}
.green:hover {
  text-shadow: none;
}
